
































import moment from "moment";
import { VueLoading } from "vue-loading-template";

export default {
  name: "News",
  props: {
    title: String,
    params: Object,
  },
  components: {
    VueLoading,
  },
  data() {
    return {
      items: Object,
      isLoading: false,
    };
  },
  created() {
    (this as any).getNews();
  },
  methods: {
    // 日付変換処理
    changeMoment(value: string): string {
      moment.locale('ja');
      return moment(value, "YYYY/MM/DD HH:mm:S").fromNow();
    },
    // 文字列制限処理
    omittedText(text: string): string {
      const MAX_LENGTH = 90;
      if (text && text.length > MAX_LENGTH) {
        return text.substr(0, MAX_LENGTH) + "...";
      }
      return text;
    },
    getPublisher(text: string): string {
      return text.substr(text.indexOf("-") + 2);
    },
    getNewsTitle(text: string): string {
      return text.substr(0, text.indexOf("-"));
    },
    // ニュース取得
    async getNews() {
      console.log("*** getNews Start : ");
      // ローディング表示
      (this as any).isLoading = true;

      const pageSize = 10;
      const baseUrl = `https://newsapi.org/v2/top-headlines?country=jp&pageSize=${pageSize}&apiKey=735dccc6a61b4ea8ac03bdb82b9395ba`;
      let addUrl = baseUrl;
      // URL編集
      if ((this as any).params && (this as any).params.category)
        addUrl += `&category=${(this as any).params.category}`;

      await (this as any).axios
        .get(addUrl)
        .then((response: any) => {
          (this as any).items = response.data.articles;
          // ローディング非表示
          (this as any).isLoading = false;
        })
        .catch((e: any) => {
          console.log(e);
          // ローディング非表示
          (this as any).isLoading = false;
        });
    },
  },
};
